import React,{useState} from 'react'
import { Button} from 'react-bootstrap'
import {Link,useHistory} from 'react-router-dom'
import {useAuth} from '../../contexts/AuthContext'
import CenteredContainer from './CenteredContainer'
import ather2 from '../../img/ather2.png'

export default function Profile()
{
    const [error, setError] = useState()
    const { logout,currentUser } = useAuth();
    const history = useHistory()

    const handleLogout = async (e) => {
        try {
            await logout();
            history.push('/login');
        } catch(error)
        {
            setError('Failed to log you out')
            console.log(error.message)
        }
    }

    return (
        <CenteredContainer>
        <div className="form-container">
            <div className="auth-heading">
                <img src={ather2} alt="ather-logo"/>
                <h2>Profile</h2>
            </div>
            {error && <p className="danger" >{error}</p>}
                {currentUser && <h2 style={{
                    width: "100%",textAlign: "center",fontSize:"1.4rem",padding:"2rem 1rem"
                }}>Email : {currentUser.email}</h2>}
                <Button className="authen-button" to="/login" variant="link" onClick={handleLogout} >Logout</Button>
                <Button className="authen-button" style={{marginTop:"2rem"}} to="/update-profile" variant="link" onClick={()=>{history.push('/update-profile')}}>Update Profile</Button>
                <Button className="authen-button" style={{marginTop:"2rem"}} to="/" variant="link" onClick={()=>{history.push('/')}}>Back to Dashboard</Button>
        </div>     
        </CenteredContainer>
    )
}
