import React,{useRef, useState} from 'react'
import {Button } from 'react-bootstrap'
import { useAuth } from '../../contexts/AuthContext'
import {Link,useHistory} from 'react-router-dom'
import CenteredContainer from './CenteredContainer'
import ather2 from '../../img/ather2.png'
import spinner from '../../img/black-spinner.gif'

export default function UpdatePassword()
{
    const emailRef = useRef()
    const passwordRef = useRef()
    const confirmPasswordRef = useRef()
    const {currentUser,updateEmail,updatePassword } = useAuth();
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()


    const handleSubmit = async (e) =>
    {
        
        e.preventDefault()
        console.log('Form was just submitted !')
        if (passwordRef.current.value !== confirmPasswordRef.current.value)
        {
            return setError('Passwords do not match')
        }

        const promises = [];
        if (emailRef.current.value !== currentUser.email)
        {
            promises.push(updateEmail(emailRef.current.value))
        }
        if (passwordRef.current.value)
        {
            promises.push(updatePassword(passwordRef.current.value))
        }

        promises.all(promises)
            .then(() => { history.push('/') })
            .catch((error) => {console.log(error); setError('Failed to update account')})

        try
        {
            setError('')
            setLoading(true)
            history.push('/')
        } catch (error)
        {
            console.log(error.message)
            setError('Failed to create an account for you')
        }
        setLoading(false)
    } 


    return (
        <CenteredContainer>
            <div className="form-container">
            <div className="auth-heading">
                <img src={ather2} alt="ather-logo"/>
                <h2>Update Profile</h2>
            </div>
            {error && <p className="danger" >{error}</p>}
            <form onSubmit={handleSubmit} >
                
                    <label htmlFor="email">Email</label>
                    <input type="email"  id="email" ref={emailRef} required >
                    </input>

                
                    <label htmlFor="password">Password</label>
                    <input placeholder="Leave blank to keep the same" type="password" id="password" ref={passwordRef} required >
                    </input>
                    
                    <label htmlFor="password">Confirm Password</label>
                    <input placeholder="Leave blank to keep the same" type="password" id="password" ref={confirmPasswordRef} required >
                    </input>
            <Button disabled={loading} className="authen-button" type="submit">{!loading ? "Submit" : <img src={spinner} style={{height:"16px", width:"16px"}} />}</Button>
            
            </form>
            </div>
            <div className="alternate-auth">
            <Link className="alternate-auth-link" to="/">Cancel</Link>
            </div>            
        </CenteredContainer>
    )
}
