import {SET_X_DETAILS, SET_GRAPH_DETAILS} from './actionTypes'
import {ENERGY_CONS} from '../reducers/graphTypes'

export const reducer = (state= {
    idRqst:['EV20201'],
    dateRqst:['01/01/2020','28/12/2020'],
    timeRqst:['00:00:00','23:59:59'],
    plotRqst:ENERGY_CONS,
    }, action) => {
    switch(action.type){
        case SET_X_DETAILS:
            console.log('X details changed !')
            return {
                ...state,
                idRqst:action.payload.idRqst,
                dateRqst:action.payload.dateRqst,
                timeRqst:action.payload.timeRqst
            }
        case SET_GRAPH_DETAILS:
            console.log('Graph details changed !')
            return{
                ...state,
                plotRqst:action.payload.graph,
            }
        default:
            return state
    }
}