import React,{useRef, useState} from 'react'
import { Button } from 'react-bootstrap'
import { useAuth } from '../../contexts/AuthContext'
import {Link,useHistory} from 'react-router-dom'
import CenteredContainer from './CenteredContainer'
import ather2 from '../../img/ather2.png'
import spinner from '../../img/black-spinner.gif'

export default function Signup()
{
    const emailRef = useRef()
    const passwordRef = useRef()
    const passwordConfirmRef = useRef()
    const { signup } = useAuth();
    const [error, setError] = useState('')
    const [loading, setLoading] = useState(false)
    const history = useHistory()


    const handleSubmit = async (e) =>
    {
        
        e.preventDefault()
        console.log('Form was just submitted !')
        if (passwordRef.current.value !== passwordConfirmRef.current.value)
        {
            return setError('Passwords do not match')
        }

        try
        {
            setError('')
            setLoading(true)
            await signup(emailRef.current.value, passwordRef.current.value)
            history.push('/')
        } catch (error)
        {
            console.log(error.message)
            setError('Failed to create an account for you')
        }
        setLoading(false)
    } 


    return (
        <CenteredContainer>
            <div className="form-container">
                    <div className="auth-heading">
                        <img src={ather2} alt="ather-logo"/>
                        <h2>Sign Up</h2>
                    </div>
                    {error && <p className="danger" >{error}</p>}
                    <form onSubmit={handleSubmit} >
                        
                            <label htmlFor="email">Email</label>
                            <input type="email"  id="email" ref={emailRef} required >
                            </input>

                        
                            <label htmlFor="password">Password</label>
                            <input type="password" id="password" ref={passwordRef} required >
                            </input>

                        
                            <label htmlFor="confirmPassword" >Confirm Password</label>
                            <input type="password" id="confirmPassword" ref={passwordConfirmRef} required >
                            </input>

                    <Button disabled={loading} className="authen-button" type="submit">{!loading ? "Submit" : <img alt="ather-logo" src={spinner} style={{height:"16px", width:"16px"}} />}</Button>
                    
                    </form>
                </div>
            <div className="alternate-auth">
                Already have an account ? <Link className="alternate-auth-link" to="/login">Login</Link>
            </div>      
        </CenteredContainer>
    )
}

