import React from 'react'
import ather from '../img/ather.png'
import {motion} from 'framer-motion'

export default function Navbar() {
    return (
        <motion.div
            initial={{y:-200, opacity:0}}
            animate={{y:0, opacity:1}}
            transition={{delay:0.2, duration:0.4, type:"spring", stiffness:120, damping:10, mass:0.2}}
            className="navbar">
            <div className="logo">
                <img src={ather} alt="Ather"/>
            </div>
            <div className="brand">
                <span className="text dash">DASH</span>
                <span className="text">BOARD</span>
            </div>
            <div className="navLinks">
                <a href="/">Dash</a>
                <a href="/data">Data</a>
                <a href="/about">About</a>
            </div>
        </motion.div>
    )
}
