import {useState, useEffect} from 'react'
import store from '../reducers/store'
import {makeQuery} from '../functions/makeQuery'

export default function useResponse() {
    const [labels, setLabels] = useState([])
    const [datasets, setDatasets] = useState([])
    const chartData = {
        labels: labels,
        datasets:datasets
    }
    
    const setChartData = async(requestBody) =>
    {
        console.log('I am about to make a query.....')
        const { idRqst,plotRqst  } = requestBody;
        const [vehicleStartId, vehicleEndId] =idRqst;
        const response = await makeQuery(requestBody)
        const postArray = Array.from(response);
        console.log(postArray)
        setLabels(postArray.map(post => post.xaxis))
        if (vehicleStartId === vehicleEndId)
        {
            setDatasets([{
                label: `${plotRqst} vs time`,
                borderColor: '#30FF00',
                backgroundColor:'#30FF00',
                data:postArray.map(post=>post.mean)
            }])
            console.log(chartData)
        } else
        {
            setDatasets([{
                label: `Minimum`,
                borderColor: '#30FF00',
                backgroundColor: '#30FF00',
                data:postArray.map(post => post.min)
            }, {
                label: `Maximum`,
                borderColor: '#47d7ff',
                backgroundColor: '#47d7ff',
                data:postArray.map(post => post.max)     
            }, {
                label: `Mean`,
                borderColor: '#4747ff',
                backgroundColor: '#4747ff',
                data:postArray.map(post => post.mean)
            }])
        }
    }
 
    useEffect(() =>
    {
        const unsubscribe = store.subscribe(() =>
        {
            const requestBody = store.getState();
            setChartData(requestBody)
        })
        return unsubscribe
    })
    return chartData
}
